class IndexPage extends React.Component {
  render() {
    
    const recipes = this.props.data.allContentfulRecipe
    return (
      
      <div>
        <ContentContainer>
          <Title>Hello, my name is Craig Coles. <br />I make things by typing numbers, letters and symbols.</Title>
          <p>I have been developing websites for the past 12 years.</p>
          <p>I am a Front End Developer at Shopify in Toronto.</p>
          <p>My tools are Javascript, CSS, HTML and coffee.</p>
        </ContentContainer>
      </div>
          
    )
  }
}

IndexPage.propTypes = propTypes

export default IndexPage

export const pageQuery = graphql`
  query PageQuery {
    allContentfulRecipe {
      edges {
        node {
          id
          title
          ingredients {
            ingredients
          }
          instructions {
            instructions
          }
        }
      }
    }
  }
`



